import React, { useRef } from "react";
import { v4 as uuid } from "uuid";
import { useInfiniteQuery } from "react-query";
import useIntersectionObserver from "../../hooks/useIntersectionObserver";
import request from "../../utils/axiosClient";
import TrackListItem from "../../components/TrackListItem/TrackListItem";
import Loader from "../../components/Loader/LoadingComponent";
import { extractOffset, extractTrackData } from "../../utils/trackUtils";
import { queryKeys, paginationLimit } from "../../constants";

const fetchRecentlyPlayed = ({ pageParams }) =>
  request.get(
    `/me/player/recently-played?limit=${paginationLimit}&offset=${pageParams}`
  );

const RecentlyPlayed = () => {
  const {
    fetchNextPage,
    hasNextPage,
    isFetchingNextPage,
    isLoading,
    isError,
    isSuccess,
    data,
  } = useInfiniteQuery(queryKeys.RECENTLY_PLAYED, fetchRecentlyPlayed, {
    getNextPageParam: (lastPage) => extractOffset(lastPage.data?.next),
  });

  const loadMoreRef = useRef(null);

  useIntersectionObserver({
    target: loadMoreRef,
    callback: () => {
      console.log("hm?");
      fetchNextPage();
    },
    options: {
      threshold: 0.6,
      defaultIntersection: false,
      once: false,
      enabled: hasNextPage,
    },
  });

  if (isLoading) {
    return (
      <div className="grid place-items-center">
        <Loader />
      </div>
    );
  }

  if (isError) {
    return <div>Failed to load.</div>;
  }

  if (isSuccess) {
    return (
      <div>
        <div>
          <h1 className="text-4xl font-bold tracking-wide dark:text-white">
            Recently Played
          </h1>
        </div>
        <div className="w-full py-16 min-w-max">
          {data.pages.map((page) => (
            <React.Fragment key={uuid()}>
              {page.data?.items?.map((track, index) => {
                const {
                  addedAt,
                  id,
                  name,
                  duration,
                  previewUrl,
                  albumId,
                  albumName,
                  artistId,
                  artistName,
                  musicImage,
                } = extractTrackData(track);
                return (
                  <TrackListItem
                    key={uuid()}
                    id={id}
                    index={index}
                    title={name}
                    duration={duration}
                    album={albumName}
                    albumId={albumId}
                    artist={artistName}
                    artistId={artistId}
                    url={previewUrl}
                    addedAt={addedAt}
                    list={page.data?.data?.items}
                    image={musicImage}
                  />
                );
              })}
            </React.Fragment>
          ))}
        </div>

        <div ref={loadMoreRef} className="h-20 w-20">
          ad
        </div>

        {isFetchingNextPage && (
          <div className="grid place-items-center">
            <Loader />
          </div>
        )}
      </div>
    );
  }

  return null;
};

export default RecentlyPlayed;
